<template>
    <div>
        <!-- <c-header :param=param></c-header> -->
        <!-- <button type="button" @click="serachfeel()">查询文章列表</button> -->

        <table class="userList" v-show="items != []" border="0" cellspacing="0">
            <tr>
                <th>序号</th>
                <th>标题</th>
                <th>作者</th>
            </tr>
            <tr v-for="(item,index) in items" keys="index">
                <td>{{index}}</td>
                <td>{{item.title}}</td>
                <td>{{item.author}}</td>
            </tr>
        </table>
    </div>
</template>

<script>

export default {
    data(){
        return {
            title:'',
            author:'',
            content:'',
            time:'',
            items:[]
        }
    },
    methods:{
        serachfeel(){
            const vm = this;
            this.axios.serachfeel({}).then(res=>{
                console.log(res)
                if(res.returnCode === '000000'){
                    vm.items = res.data.doc;
                }
            })
        },

    },
    mounted() {
        this.serachfeel()
    },
}
</script>




